import React from 'react';
import sql from 'mssql';

import SeleccionarTabla from './SeleccionarTabla.js';
import SeleccionarCredito from './SeleccionarCredito.js';
import SeleccionarRegla from './SeleccionarRegla.js';
import GuardarTipoCreditoCampo from './GuardarTipoCreditoCampo.js';

export default class TipoCredito extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            idTablaSeleccionada: -1,
            idCreditoSeleccionado: -1,
            mostrarTabla: "selTable",
            regla: {}
        }
        this.updateTableSelectedID = this.updateTableSelectedID.bind(this);
        this.updateCreditSelectedID = this.updateCreditSelectedID.bind(this);
        this.updateVarCreation = this.updateVarCreation.bind(this);
        this.returnChooseTable = this.returnChooseTable.bind(this);
        this.returnSelCredit = this.returnSelCredit.bind(this);
        this.returnVarCreation = this.returnVarCreation.bind(this);
    }

    updateTableSelectedID (id) {
        this.setState({
            idTablaSeleccionada: id,
            mostrarTabla: "selCredit"
        });
    }

    updateCreditSelectedID (id) {
        this.setState({
            idCreditoSeleccionado: id,
            mostrarTabla: "selVar"
        });
    }

    updateTypeCredit (id) {
        this.setState({
            idCreditoSeleccionado: id,
            mostrarTabla: "saveTypeCredit"
        });
    }

    updateVarCreation (id) {
        this.setState({
            idCreditoSeleccionado: id,
            mostrarTabla: "saveTypeCredit"
        });
    }

    returnChooseTable () {
        this.setState({
            idTablaSeleccionada: this.state.idTablaSeleccionada,
            mostrarTabla: "selTable"
        });
    }

    returnSelCredit () {
        this.setState({
            idCreditoSeleccionado: this.state.idCreditoSeleccionado,
            mostrarTabla: "selCredit"
        });
    }

    returnVarCreation = (campotablaID, campoCampoID, campoTipo, operacion, tipoOperacion, valor, valortipo, esListaValor, esCampoValor, valortablaID, valorCampoID) => {
        this.setState({
            regla: {campotablaID: campotablaID, campoCampoID: campoCampoID, campoTipo: campoTipo, operacion: operacion, tipoOperacion: tipoOperacion, valor: valor, valortipo: valortipo, esListaValor: esListaValor, esCampoValor: esCampoValor, valortablaID: valortablaID, valorCampoID: valorCampoID},
            mostrarTabla: "saveTypeCreditField"
        });
    }

    render() {
        if(this.state.mostrarTabla.localeCompare("selTable") == 0) {
            return (
                <div>
                    <div className={"row"}>
                        <div className={"col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12"}>
                            <div className={"page-header"}>
                                <h2 className={"pageheader-title"}>Configuraci&oacute;n</h2>
                                <div className={"page-breadcrumb"}>
                                    <nav aria-label="breadcrumb">
                                        <ol className={"breadcrumb"}>
                                            <li className={"breadcrumb-item"} aria-current="page" onClick={this.props.showConfigurationComponent}><a href="#" className={"breadcrumb-link"}>Configuraci&oacute;n</a></li>
                                            <li className={"breadcrumb-item active"} aria-current="page">Seleccionar Tabla</li>
                                        </ol>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                    <SeleccionarTabla pool={this.props.pool} seleccionarTabla={this.updateTableSelectedID}> </SeleccionarTabla>
                </div>
            );
        } else if(this.state.mostrarTabla.localeCompare("selCredit") == 0) {
            return (
                <div>
                    <SeleccionarCredito pool={this.props.pool} seleccionarCredito={this.updateCreditSelectedID} showConfigurationComponent={this.props.showConfigurationComponent} retornoTablas={this.returnChooseTable} tablaID={this.state.idTablaSeleccionada}> </SeleccionarCredito>
                </div>
            );
        } else if(this.state.mostrarTabla.localeCompare("selVar") == 0) {
            return (
                <div>
                    <SeleccionarRegla pool={this.props.pool} tablaID={this.state.idTablaSeleccionada} showConfigurationComponent={this.props.showConfigurationComponent} retornoTablas={this.returnChooseTable} returnSelCredit={this.returnSelCredit}> </SeleccionarRegla>
                </div>
            );
        } else {
            return (
                <div>
                    <div className={"row"}>
                        <div className={"col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12"}>
                            <div className={"page-header"}>
                                <h2 className={"pageheader-title"}>Configuraci&oacute;n</h2>
                                <div className={"page-breadcrumb"}>
                                    <nav aria-label="breadcrumb">
                                        <ol className={"breadcrumb"}>
                                            <li className={"breadcrumb-item"} aria-current="page" onClick={this.props.showConfigurationComponent}><a href="#" className={"breadcrumb-link"}>Configuraci&oacute;n</a></li>
                                            <li className={"breadcrumb-item"} aria-current="page" onClick={this.props.returnChooseTable}><a href="#" className={"breadcrumb-link"}>Seleccionar Tabla</a></li>
                                            <li className={"breadcrumb-item"} aria-current="page" onClick={this.returnSelCredit}><a href="#" className={"breadcrumb-link"}>Seleccionar Tipo de Cr&eacute;dito</a></li>
                                            <li className={"breadcrumb-item active"} aria-current="page">Creaci&oacute;n de Variables</li>
                                        </ol>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                    <GuardarTipoCreditoCampo pool={this.props.pool}> </GuardarTipoCreditoCampo>
                </div>
            );
        }
    }
}
